<template>
  <div id="container" class="container">
    <img class="banner" :src="testImg" alt />
    <div class="container-title"></div>
    <el-tabs class="container-tabs" tab-position="left">
      <el-tab-pane class="tabs-pane" label="">
        <div class="tabs-content">
          <div class="item" v-for="(item, index) in news" :key="index">
            <el-image :src="item.pic" alt fit="cover" />
            <div class="info">
              <div class="title">{{item.title}}</div>
              <div class="time">{{item.date}}</div>
              <p class="note">{{item.note}}</p>
              <a class="btn" :href="'/news/newsDetail/' + item.id" target="_blank"></a>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane class="tabs-pane" label="">
        <div class="tabs-content">
          <div class="item" v-for="(item, index) in infos" :key="index">
            <el-image :src="item.pic" alt fit="cover" />
            <div class="info">
              <div class="title">{{item.title}}</div>
              <div class="time">{{item.date}}</div>
              <p class="note">{{item.note}}</p>
              <a class="btn" :href="'/news/newsDetail/' + item.id" target="_blank"></a>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="pagi-wrapper">
      <el-pagination
        class="pagi"
        background
        :page-size="5"
        :total="currentType === 1 ? newsCount : infosCount"
      />
    </div>
    <!-- <div class="container-right">
      <ul>
        <li v-for="(item, index) in (currentType === 1 ? news : infos)" :key="index"></li>
      </ul>
    </div>-->
  </div>
</template>

<script>
import axios from "axios";
import { getInfos, getNews } from "@/utils/service";
import NewsCard from "./Components/NewsCard";
import router from "../../router";

export default {
  name: "container",
  components: {
    "news-card": NewsCard
  },
  data() {
    return {
      news: undefined,
      infos: undefined,
      newsCount: undefined,
      infosCount: undefined,
      currentType: 1,
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {},
  mounted() {
    getInfos(1, 5).then(v => {
      if (!v) {
        return;
      }

      const { ok, data, count } = v;
      if (ok) {
        const newData =
          v.data &&
          v.data.map(v => {
            v.note = v.content.substring(0, 100);
            return v;
          });
        this.infos = newData;
        this.infosCount = v.count;
      } else {
        $message("");
      }
    });

    getNews(1, 5).then(v => {
      if (!v) {
        return;
      }

      const { ok, data, count } = v;
      if (ok) {
        const newData =
          v.data &&
          v.data.map(v => {
            v.note = v.content.substring(0, 20);
            return v;
          });
        this.news = newData;
        this.newsCount = v.count;
      } else {
        $message("");
      }
    });
  },
  methods: {
    onCardClick: function(id) {
      window.open(`/news/newsDetail/${id}`);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
