<template>
  <div id="container" class="container">
    <div class="container-title"></div>
    {{data}}
  </div>
</template>

<script>
import axios from 'axios';
import { getNewsById } from '@/utils/service';
import NewsCard from '../Components/NewsCard';

export default {
  name: 'container',
  components: {
    "news-card": NewsCard,
  },
  props: {
    currentType: Number,
  },
  data() {
    return {
      data: undefined,
    };
  },
  filters: {},
  mounted() {
    getNewsById(this.$route.params.id).then(v => {
      if (!v) {
        return;
      }

      const { ok, data} = v;
      if (ok && data && data.length !== 0) {
        this.data = v.data[0];
      } else {
        $message("");
      }
    });
  },
};
</script>
<style lang="scss" scoped>
  @import './index.scss';
</style>