import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/Home/index.vue';
import News from '@/views/News/index.vue';
import NewsDetail from '@/views/News/NewsDetail/index.vue';
import Prod from '@/views/Prod/index.vue';
import Appearance from '@/views/Appearance/index.vue';
import Service from '@/views/Service/index.vue';
import About from '@/views/About/index.vue';
import Contact from '@/views/Contact/index.vue';
import NotFoundPage from '@/views/NotFoundPage.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    meta: {
      title: '',
    },
    component: Home,
  },
  {
    path: '/news',
    name: 'news',
    component: News,
  },
  {
    path: '/news/newsDetail/:id',
    name: 'newsDetail',
    component: NewsDetail,
  },
  {
    path: '/prod',
    name: 'prod',
    component: Prod,
  },
  {
    path: '/appearance',
    name: 'appearance',
    component: Appearance,
  },
  {
    path: '/service',
    name: 'service',
    component: Service,
  },
  {
    path: '/about',
    name: 'about',
    component: About,
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact,
  },
  {
    path: '/404',
    name: 'notFound',
    component: NotFoundPage,
  },
  {
    path: '*', // 
    redirect: '/404',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  // if(to.meta.title) {
  //   document.title = to.meta.title;
  // }
  next();
})

export default router;
