                class="company-img"
                src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
                fit="cover"
              />
              <div class="company-card-info" style="padding: 14px;">
                <span></span>
                <p>OA</p>
              </div>
            </el-card>
            <el-card class="company-card" :body-style="{ padding: '0px' }">
              <el-image
                class="company-img"
                src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
                fit="cover"
              />
              <div class="company-card-info" style="padding: 14px;">
                <span></span>
                <p>OA</p>
              </div>
            </el-card>
          </div>
        </div>
      </section>
      <section class="card-pic">
        <div class="card-header">
          <h2 class="card-header-title"></h2>
          <div class="card-header-subtitle"> </div>
          <el-link class="card-header-more" href="/prod" target="_blank">
            
            <i class="el-icon-circle-plus-outline" />
          </el-link>
        </div>
        <el-carousel
          class="card-content"
          arrow="always"
          indicator-position="outside"
          :interval="5000"
          type="card"
          height="25vw"
        >
          <el-carousel-item class="card-item" v-for="(item, index) in prods" :key="index">
            <el-image class="card-item-img" :src="item.pic" fit="cover" />
          </el-carousel-item>
        </el-carousel>
