      <section class="card-news">
        <div class="news-header">
          <h2 class="news-title"></h2>
          <el-link class="card-header-more" href="/news" target="_blank">
            
            <i class="el-icon-circle-plus-outline" />
          </el-link>
        </div>
        <div class="news-content">
          <el-card class="card-item" v-for="(item, index) in news" :key="index" :body-style="{ padding: 0 }">
            <el-image class="card-item-img" :src="item.pic" fit="cover" />
            <div class="card-item-content">
              <div class="card-item-date">{{item.date}} | {{item.type === 1 ? '' : ''}}</div>
