<template>
  <div id="container" class="container">
    <img class="banner" :src="testImg" alt />
    <div class="main">
      <h2 class="main-title"></h2>
      <div class="main-content">
        <el-card 
        class="content-card" 
        v-for="(item, index) in appearances" 
        :key="index"
        :body-style="{ padding: '0.5vw' }"
        >
          <el-image class="card-img" :src="item.pic" fit="cover" :preview-src-list="[item.pic]" />
          <div class="card-title">{{item.title}}</div>
        </el-card>
      </div>
      <div class="pagi-wrapper">
        <el-pagination
          class="pagi"
          background
          :page-size="5"
          :total="count"
          @current-change="onPageChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getAppearances } from "@/utils/service";

export default {
  name: "container",
  components: {},
  data() {
    return {
      appearances: undefined,
      count: undefined,
      testImg: "https://s2.ax1x.com/2019/12/15/QWXQW8.png"
    };
  },
  filters: {},
  mounted() {
    this.fetchData(1, 8);
  },
  methods: {
    fetchData(pageIndex = 1, pageSize = 8) {
      getAppearances(pageIndex, pageSize).then(v => {
        if (!v) {
          return;
        }
        
        const { ok, data, count } = v;
        if (ok) {
          this.appearances = data;
          this.count = count;
        }
      });
    },
    onPageChange(i) {
      this.fetchData(i, 8);
    }
  }
};
</script>
<style lang="scss" scoped>
  @import './index.scss';
</style>