import axios from 'axios';
import router from '../router';
import store from '../store/index';

axios.defaults.baseURL = 'http://honoo.xyz:4433';

/**
 * 
 * 
 */
const tip = (msg: string) => {
    // Toast({
    //     message: msg,
    //     duration: 1000,
    //     forbidClick: true,
    // });
};

/**
 * 
 * 
 */
const toLogin = () => {
    // router.replace({
    //     path: '/login',
    //     query: {
    //         redirect: router.currentRoute.fullPath,
    //     },
    // });
};

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status: number, other: string) => {
    // 
    switch (status) {
        // 401: 
        case 401:
            toLogin();
            break;
        // 403 token
        // token
        case 403:
            tip('');
            localStorage.removeItem('token');
            store.commit('loginSuccess', null);
            setTimeout(() => {
                toLogin();
            }, 1000);
            break;
        // 404
        case 404:
            tip('');
            break;
        default:
            console.log(other);
    }
};

// axios
const instance = axios.create({ timeout: 1000 * 12 });
// post
instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
/**
 * 
 * tokentoken
 */
instance.interceptors.request.use(
    (config) => {
        // token
        // tokentokentoken
        // token
        // 
        const token = (store.state as any).token;
        if (token) {
            config.headers.Authorization = token;
        }
        return config;
    },
    (error) => Promise.reject(error));

// 
instance.interceptors.response.use(
    // 
    (res) => {
        if (process.env.NODE_ENV !== 'production') {
            console.group(` ${res.config.url}`);
            console.log(res.data);
            console.groupEnd();
        }
        if (res.status === 200) {
            return Promise.resolve({ ...res, ok: 1 });
        } else {
            return Promise.reject({ ...res, ok: 0 });
        }
    },
    // 
    (error) => {
        const { response } = error;
        if (process.env.NODE_ENV !== 'production') {
            console.group(` ${response.config.url}`);
            console.log(response.data);
            console.groupEnd();
        }

        if (response) {
            // 2xx
            errorHandle(response.status, response.data.message);
            return Promise.reject({ ...response, ok: 0 });
        } else {
            // 
            // eg:statenetwork
            // networkapp.vue
            // 
            if (!window.navigator.onLine) {
                store.commit('changeNetwork', false);
            } else {
                return Promise.reject({ ...error, ok: 0 });
            }
        }
    });

export const fetch = (url: string, params?: object) => {
    return instance.get(url, params).catch(() => ({ error: 1 }));
};

export default instance;
